*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
    Cantarell, sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.card {
  width: 470px;
}

.card-title {
  text-align: center;
  margin-bottom: 20px;
}

.card-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.card-text {
  margin-top: 0;
  margin-bottom: 10px;
}

.card-img {
  display: flex;
  justify-content: center;
}

.card {
  margin: 10px;
}

.country-input {
  width: 470px;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}

.country-input:focus {
  border: 3px solid #555;
}
